.post {
    width: 50vw;
    min-width: 500px;
    margin-top: 5px;
    border-radius: 15px;
    background-color: white;
    box-shadow: 5px 5px 30px -7px rgba(0, 0, 0, 0.75);
    margin-right:10px;
    margin-left:10px;
}

.post_top {
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
}

.post_avatar {
    margin-right: 10px;
}

.post_topInfo > h6 {
    font-size: medium;
}

.post_topInfo > p {
    font-size: small;
    color: gray;
}

.post_bottom {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 5px 25px;
}

.post_image > img {
    width: 50vw;
    height: 300px;
    min-width: 500px;
    min-height: 30vw;
    padding-left: 0px;
}

.post_video > video {
    width: 50vw;
    height: 300px;
    min-width: 500px;
    min-height: 30vw;
    padding-left: 0px;
    outline-width:0;
}

.post_options {
    padding-top: 10px;
    border-top: 1px solid lightgray;
    display: flex;
    justify-content: space-evenly;
    font-size: medium;
    color: gray;
    cursor: pointer;
    padding: 15px;
}

.post_option {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    flex: 1;
}

.post_option > p {
    margin-left: 5px;
}

.post_option:hover {
    background-color: #eff2f5;
    border-radius: 10px;
}