.messageSender1 {
    display: flex;
    justify-content: space-between;
    position: sticky;
    background-color: rgb(255, 255, 255);
    width: 100%;
    box-shadow: 5px 5px 8px -8px rgba(0, 0, 0, 0.75);
    flex: 1;
}

.messageSender_top1 {
    display: flex;
    padding-bottom: 15px;
    padding: 15px;
    width:100%
}


.messageSender_top1 > form {
    flex: 1;
    display: flex;
}

.messageSender_top1 > form > input {
    outline-width: 0;
    border: none;
    padding: 5px 20px;
    margin: 0px 10px;
    border-radius: 999px;
    background-color: #eff2f5;
}

.messageSender_input1 {
    flex: 1;
}
