.messageSender {
    display: flex;
    margin-top: 30px;
    padding-top: 10px;
    flex-direction: column;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 30px -7px rgba(0, 0, 0, 0.75);
    width: 50vw;
    min-width: 500px;
    margin-right:10px;
    margin-left:10px;
}

.messageSender_top {
    display: flex;
    border-bottom: 1px solid #eff2f5;
    padding-bottom: 15px;
    padding: 15px 15px;
    margin-right:5px;
}

.messageSender_top > form {
    flex: 1;
    display: flex;
}

.messageSender_top > form > input {
    outline-width: 0;
    border: none;
    padding: 5px 20px;
    margin: 0px 10px;
    border-radius: 999px;
    background-color: #eff2f5;
}

.messageSender_top > form > button {
    display: none;
}

.messageSender_input {
    display: flex;
    flex: 1;
}


.messageSender_image {
    width: 130px;
}

.messageSender_bottom {
    display: flex;
    justify-content: space-evenly;
}


.dialog0 {
     width: 300px;
     justify-content: center;
    padding: 20px 60px;
}

.dialog0 > img {
    display: flex;
    flex-direction: row;
    width: 250px;
    height: 250px;
    min-width: 150px;
    min-height: 150px;
    margin: 0px -20px;
    padding: 20px 45px 40px;
    justify-content:center;
}

.dialog0 > video {
    display: none;
    flex-direction: row;
    width: 250px;
    height: 250px;
    min-width: 150px;
    min-height: 150px;
    margin: 0px -20px;
    padding: 20px 45px 40px;
    justify-content:center;
    outline-width:0;
}

.dialog_head {
    display: flex;
      justify-content: center;
}

.dialog1 {
    display: flex;
    flex-direction: row;
    margin: 0px -30px;
    padding-right: 15px;
    justify-content: space-between;
}

.dialog2 {
    display: none;
     flex-direction: row;
    margin: 0px -30px;
    padding-right: 15px;
    justify-content: space-between;
}

.messageSender_option {
    padding: 10px;
    display: flex;
    align-items: center;
    color: gray;
    margin: 5px;
}

.messageSender_option > h3 {
    font-size: medium;
    margin-left: 10px;
    cursor: pointer;
}

.messageSender_option:hover {
    background-color: #eff2f5;
    border-radius: 20px;
}