.story1{
    position: relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 120px;
    height: 200px;
    box-shadow: 0px 5px 17px -7px rgba(0, 0, 0, 0.75);
    border-radius: 10px;
    margin-right: 20px;
    transition: transform 100ms ease-in;
    cursor: pointer;
}


.story_avatar1 {
    margin: 10px;
    border: 5px solid #1d5ac5;
}

.story1 > h6{
    position: absolute;
    bottom: 20px;
    left: 20px;
    color: red;
}


